@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&display=swap");

:root {
  --clr-buttons: #333;
}

* {
  box-sizing: border-box;
}

body {
  padding: 2rem;
  height: 100vh;
  font-family: "Poppins";
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10rem;
  height: 95vh;
}

.columns {
  display: flex;
  flex-direction: column;
}

.pressed-box {
  height: 600px;
  width: 600px;
  background: white;
  border: 3px dashed #333;
  border-radius: 2rem;
}

.red {
  background: red !important;
}

.countdown-container {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 15rem;
  font-weight: 900;
  color: green;
}

.green-text {
  color: var(--clr-buttons);
}

.welcome-message {
  width: 700px;
  height: 600px;
  background-color: #ddd;
  padding-inline: 5rem;
  padding-top: 3rem;
  padding-bottom: 5rem;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.2);
  border-radius: 1rem;
  border: 1px solid #bbb;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.welcome-message div {
  width: 100%;
}

.start {
  color: red;
  font-size: 1.4rem;
  font-weight: 600;
  text-align: center;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.end-download {
  font-size: 0.8rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.btn-download {
  font-size: 1rem;
  padding: 0.7rem;
  margin-right: 1rem;
  border-radius: 5px;
  outline: none;
  border: none;
  background-color: var(--clr-buttons);
  color: white;
}

.btn-start {
  font-size: 1rem;
  padding: 0.7rem;
  margin-right: 1rem;
  border-radius: 5px;
  outline: none;
  border: none;
  background-color: var(--clr-buttons);
  color: white;
  padding-inline: 2rem;
  font-size: 1.3rem;
}

.btn-start:active,
.btn-download:active {
  transform: scale(0.98);
}

.purple {
  background: #666;
  padding-inline: 1rem;
}
